.contact{padding:4rem 3rem 10rem 3rem;background:rgba(247,235,208,.4)}.contact .contactLayout{display:grid;grid-template-columns:55% 1fr;gap:4rem;align-items:start}.contact .mainTitle{color:var(--quaternary);font-size:2.5rem;margin-bottom:2rem}.contact .formSection .contactForm{display:flex;flex-direction:column;gap:1rem;border:2px solid var(--quaternary-light);border-radius:12px;padding:1rem}.contact .formSection .formGroup{display:flex;flex-direction:column;gap:.5rem}.contact .formSection .formGroup label{color:var(--gray-800);font-weight:600;font-size:.95rem}.contact .formSection .formGroup input,.contact .formSection .formGroup textarea{padding:1rem;border:2px solid var(--quaternary-light);border-radius:12px;font-size:1rem;font-family:inherit;transition:border-color .3s ease;color:var(--text-quaternary)}.contact .formSection .formGroup input:focus,.contact .formSection .formGroup textarea:focus{outline:none;border-color:var(--quaternary)}.contact .formSection .formGroup textarea{resize:vertical;min-height:120px}.contact .formSection .submitButton{padding:1rem 2rem;background:var(--quaternary);color:var(--white);border:none;border-radius:8px;font-size:1.1rem;font-weight:600;cursor:pointer;transition:all .3s ease}.contact .formSection .submitButton:hover:not(:disabled){background:var(--accent);transform:translateY(-2px);box-shadow:0 5px 15px rgba(0,0,0,.2)}.contact .formSection .submitButton:disabled{opacity:.6;cursor:not-allowed}.contact .formSection .successMessage{padding:1rem;background:rgba(76,175,80,.1);color:#4caf50;border-radius:8px;border-left:4px solid #4caf50;font-weight:500}.contact .formSection .errorMessage{padding:1rem;background:rgba(244,67,54,.1);color:#f44336;border-radius:8px;border-left:4px solid #f44336;font-weight:500}.contact .infoSection{display:flex;flex-direction:column;gap:2rem}.contact .mapContainer{width:100%;height:300px;border-radius:16px;overflow:hidden;box-shadow:0 5px 20px rgba(0,0,0,.1)}.contact .mapContainer iframe{width:100%;height:100%}.contact .contactInfo{display:flex;flex-direction:column;gap:2.5rem}.contact .iconWrapper{flex-shrink:0;width:32px;height:32px;display:flex;align-items:center;justify-content:center;background:rgba(69,97,173,.1);border-radius:50%;color:var(--quaternary);overflow:hidden}.contact .iconWrapper svg{width:16px;height:16px;display:block;overflow:visible}.contact .infoTitle{color:var(--quaternary);font-size:1.8rem;margin-bottom:.5rem;font-weight:600}.contact .addressInfo{display:flex;align-items:flex-start;gap:1rem}.contact .addressInfo .address{color:var(--gray-800);font-size:1.1rem;font-weight:500;margin-bottom:.25rem}.contact .addressInfo .city{color:var(--gray-700);font-size:1rem}.contact .contactDetails{display:flex;flex-direction:row;flex-wrap:wrap;gap:1.5rem;align-items:center}.contact .contactDetails .contactItem{display:flex;align-items:center;gap:.75rem}.contact .contactDetails .contactItem a{color:var(--quaternary);text-decoration:none;font-size:1rem;font-weight:500;transition:color .3s ease;white-space:nowrap}.contact .contactDetails .contactItem a:hover{color:var(--quaternary-text);text-decoration:underline}.contact .contactDetails .phone,.contact .contactDetails .email{color:var(--gray-700);font-size:1rem}.contact .contactDetails .phone a,.contact .contactDetails .email a{color:var(--quaternary);text-decoration:none;transition:color .3s ease}.contact .contactDetails .phone a:hover,.contact .contactDetails .email a:hover{color:var(--quaternary-text);text-decoration:underline}.contact .transportInfo{padding-top:2rem;border-top:2px solid var(--gray-200)}.contact .transportInfo .transportTitle{color:var(--quaternary);font-size:1.1rem;font-weight:600;margin-bottom:1.25rem}.contact .transportInfo .transportDetails{display:flex;flex-direction:column;gap:1rem}.contact .transportInfo .transportDetails .transportItem{display:flex;align-items:center;gap:1rem;color:var(--gray-700);font-size:.95rem}.contact .transportInfo .transportDetails .transportItem strong{color:var(--quaternary)}@media(max-width: 1024px){.contact .contactLayout{grid-template-columns:1fr;gap:3rem}.contact .mapContainer{height:250px}}@media(max-width: 768px){.contact{padding:2rem 1rem}.contact .contactLayout{gap:2rem}.contact .sectionTitle,.contact .infoTitle{font-size:1.5rem}.contact .mapContainer{height:200px}.contact .formSection .formGroup input,.contact .formSection .formGroup textarea{padding:.75rem}.contact .formSection .submitButton{padding:.875rem 1.5rem;font-size:1rem}}