.partnerships{background:linear-gradient(180deg, var(--white) 25%, rgba(247, 235, 208, 0.4) 100%);padding-inline:3rem}.partnerships .header{margin-bottom:1rem}.partnerships .title{color:var(--text-primary);font-size:2.5rem;margin-bottom:1rem}.partnerships .subtitle{font-size:1.2rem;color:var(--accent);max-width:700px}.partnerships .sliderWrapper{position:relative;display:flex;align-items:center;gap:1rem;margin-bottom:2rem}.partnerships .sliderButton{flex-shrink:0;width:50px;height:50px;border-radius:50%;border:2px solid var(--accent);background:var(--white);color:var(--accent);font-size:2rem;cursor:pointer;display:flex;align-items:center;justify-content:center;transition:all .3s ease}.partnerships .sliderButton:hover{background:var(--accent);color:var(--white);transform:scale(1.1)}.partnerships .sliderButton:active{transform:scale(0.95)}.partnerships .slider{flex:1;display:flex;overflow-x:hidden;scroll-behavior:smooth;scroll-snap-type:x mandatory;-webkit-overflow-scrolling:touch;scrollbar-width:none}.partnerships .slider::-webkit-scrollbar{display:none}.partnerships .slide{min-width:100%;scroll-snap-align:start;padding:1rem 1rem}.partnerships .partnerCard{background:var(--white);padding:2rem;border-radius:16px;display:flex;align-items:center;gap:2rem;box-shadow:0 0px 15px rgba(0,0,0,.1)}.partnerships .logoPlaceholder{flex-shrink:0;width:120px;height:120px;background:linear-gradient(135deg, var(--primary), var(--accent));border-radius:16px;display:flex;align-items:center;justify-content:center;color:#fff;font-size:2rem;font-weight:bold;box-shadow:0 5px 15px rgba(0,0,0,.1)}.partnerships .partnerInfo{flex:1}.partnerships .partnerName{color:var(--text-primary);font-size:1.5rem;margin-bottom:.5rem;font-weight:600}.partnerships .partnerCategory{color:var(--accent);font-size:1rem;font-weight:500}.partnerships .dots{display:flex;justify-content:center;gap:.5rem}.partnerships .dot{width:12px;height:12px;border-radius:50%;border:2px solid var(--accent);background:rgba(0,0,0,0);cursor:pointer;transition:all .3s ease}.partnerships .dot:hover{background:var(--accent);opacity:.7}.partnerships .dot.active{background:var(--accent);width:32px;border-radius:6px}.partnerships .certifications{background:var(--secondary-color);padding:3rem;border-radius:16px;text-align:center}.partnerships .certifications h3{color:var(--primary);font-size:2rem;margin-bottom:2rem}.partnerships .certifications .certList{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));gap:1.5rem}.partnerships .certifications .certItem{background:var(--white);padding:1.5rem;border-radius:8px;box-shadow:0 5px 15px rgba(0,0,0,.1)}.partnerships .certifications .certItem strong{color:var(--primary);display:block;font-size:1.1rem;margin-bottom:.5rem}@media(max-width: 768px){.partnerships{padding-inline:1rem}.partnerships .header{margin-bottom:2rem}.partnerships .title{font-size:2rem}.partnerships .subtitle{font-size:1rem}.partnerships .sliderWrapper{gap:.5rem}.partnerships .sliderButton{width:40px;height:40px;font-size:1.5rem}.partnerships .partnerCard{flex-direction:column;text-align:center;padding:1.5rem;gap:1.5rem}.partnerships .logoPlaceholder{width:100px;height:100px;font-size:1.5rem;margin:0 auto}.partnerships .partnerName{font-size:1.2rem}.partnerships .partnerCategory{font-size:.9rem}}